.App {
  text-align: center;
}



.App-header {
  height: 100vh;
  background: linear-gradient(-45deg, #000000, rgb(60, 14, 36), rgb(31, 13, 63), #1b183e);
  background-size: 300% 300%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
